require('./timeline-calendar-frame.css')

import React from 'react'

import Timeline from './timeline.jsx'
import Ruler from './ruler.jsx'
import DU, {addToMoment, getCurrentOrNextMoment} from '../../../js/time/dateutils.js'
import TimeUnit from '../../../js/time/timeunit.js'
import {TimelineCalendarForm} from './form/timeline-calendar-form.jsx'

const viewBow = {
  width:{
    timeline:500,
    leftpane:10
  },
  height:{
    timeline:20
  }
}

export class TimelineCalendarFrame extends React.Component{

  constructor(props) {
    super(props)
    this.state = {}
    this.handleFormChange = this.handleFormChange.bind(this)
  }

  handleFormChange(formState){
    this.setState(formState)
  }

  get startDateFormat(){
    if(this.state.startDate){
      return this.state.startDate.format();
    }
    return null;
  }

  get endDate(){
    return addToMoment(this.state.startDate, this.state.timeunit, this.state.unitnumber)
  }

  get endDateFormat(){
    let endDate = this.endDate
    if(endDate){
      return endDate.format();
    }
    return null;
  }

  render(){
    let viewBoxWidth = viewBow.width.timeline + viewBow.width.leftpane;
    let viewbox = '0 0 ' + viewBoxWidth + ' 110'
    let maxNumber = 250;
    let rulerIndex = computeRulerIndex(viewBow.width.timeline, viewBow.width.leftpane, maxNumber, this.state)
    let rulers = rulerIndex ? TimeUnit.values.filter(tu => rulerIndex[tu]).map((tu, i) => <Ruler key={i} y={102 + i * 3} index={rulerIndex[tu]} color={rulersColor.get(tu)}/>) : [];
    return (
      <div>
        <h1>Timeline Calendar Frame</h1>
        <div> Timeunit: {this.state.timeunit} - number: {this.state.unitnumber} - startDate: {this.startDateFormat} - endDate: {this.endDateFormat}</div>
        {/*
          https://www.sarasoueidan.com/blog/svg-coordinate-systems
          https://www.w3schools.com/graphics/svg_intro.asp
        */}
        <div className="svg-container">
          <svg version="1.1" viewBox={viewbox} preserveAspectRatio="xMinYMin meet" className="svg-content">
            <Timeline position={1} startPoint={50} endPoint={150} color="red" leftPaneWidth={viewBow.width.leftpane} height={viewBow.height.timeline} />
            <Timeline position={2} startPoint={20} endPoint={40} color="blue" leftPaneWidth={viewBow.width.leftpane} height={viewBow.height.timeline} />
            <Timeline position={3} startPoint={90} endPoint={410} color="pink" leftPaneWidth={viewBow.width.leftpane} height={viewBow.height.timeline} />
            <Timeline position={4} startPoint={120} endPoint={390} color="purple" leftPaneWidth={viewBow.width.leftpane} height={viewBow.height.timeline} />
            <Timeline position={5} startPoint={40} endPoint={500} color="brown" leftPaneWidth={viewBow.width.leftpane} height={viewBow.height.timeline} />
            {rulers}
          </svg>
        </div>
        <TimelineCalendarForm publish={this.handleFormChange}/>
      </div>
    )
  }
}

function computeRulerIndexBck(x_width, offset, maxNumber, {startDate, timeunit, unitnumber}){
  if(!(startDate && timeunit && unitnumber)){
    return null;
  }
  let rulerUnit = computeRulerUnit(startDate, timeunit, unitnumber);
  let rulerIndex = {};
  TimeUnit.values.filter(timeunit => rulerInfo[timeunit]).forEach(timeunit => {
    let timeUnitIndex = new Map();
    let number = rulerInfo[timeunit];
    let x_interval = x_width / number;
    let firstPoint = ceil(startDate, timeunit)
    for(let i=0; i<=number; i++){
      let keyMoment = addToMoment(firstPoint, timeunit, number * i);
      timeUnitIndex.set(keyMoment, offset + x_interval * i);
    }
    rulerIndex[timeunit] = timeUnitIndex;
  })
  return rulerIndex;
}

function computeRulerUnitBck(startDate, reftimeunit, refunitnumber){
  let result = {};

  //define the displayed date range of the ruler
  result.displayedRangeStart = DU.floor(startDate, reftimeunit)
  result.displayedRangeEnd = DU.addToMoment(startDate, reftimeunit, refunitnumber)

  TimeUnit.values.forEach((tu) => {
    let tuInfo = {}
    tuInfo.unit = tu
    //define the start of the current time unit
    tuInfo.startDate = DU.floor(result.displayedRangeStart, tu)
    /*
      while the start of the current unit is before the start of the requested unit
      we add one current unit.
      (For instance, if the requested unit is month,
        the start of the first week of the month can be before the start of the current month)
    */
    while(tuInfo.startDate.isBefore(result.displayedRangeStart)){
      tuInfo.startDate = DU.addToMoment(tu.startDate, tu, 1)
    }
    //define the end of the current time unit
    tuInfo.endDate = DU.ceil(result.displayedRangeEnd, tu)
    /*
      while the end of the current unit is after the end of the requested unit
      we substract one current unit.
      (For instance, if the requested unit is month,
        the end of the last week of the month can be before the end of the current month)
    */
    while(tuInfo.endDate.isAfter(result.displayedRangeEnd)){
      tuInfo.endDate = DU.addToMoment(tu.enDate, tu, -1)
    }
    tuInfo.duration = result.endDate.diff(result.startDate)
    switch(tu){
      case TimeUnit.YEAR:
        tuInfo.number = Math.ceil(duration.asYears());
        break
      case TimeUnit.MONTH:
        tuInfo.number = Math.ceil(duration.asMonths());
        break
      case TimeUnit.WEEK:
        tuInfo.number = Math.ceil(duration.asWeeks());
        break
      case TimeUnit.DAY:
        tuInfo.number = Math.ceil(duration.asDays());
        break
      case TimeUnit.HOUR:
        tuInfo.number = Math.ceil(duration.asHours());
        break
      case TimeUnit.MINUTES_15:
        tuInfo.number = Math.ceil(duration.asMinutes() / 15);
        break
      case TimeUnit.MINUTES_5:
        tuInfo.number = Math.ceil(duration.asMinutes() / 5);
        break
      case TimeUnit.MINUTE:
        tuInfo.number = Math.ceil(duration.asMinutes());
    }
    result[tu] = tuInfo
  });


  return result;
}


function computeRulerIndex(x_width, offset, maxNumber, {startDate, timeunit, unitnumber}){
  if(!(startDate && timeunit && unitnumber)){
    return null;
  }
  let rulerInfo = computeRulerInfo(maxNumber, timeunit, unitnumber);
  let rulerIndex = {};
  TimeUnit.values.filter(timeunit => rulerInfo[timeunit]).forEach(timeunit => {
      let timeUnitIndex = new Map();
      let number = rulerInfo[timeunit];
      let x_interval = x_width / number;
      for(let i=0; i<=number; i++){
        let keyMoment = addToMoment(startDate, timeunit, number * i);
        timeUnitIndex.set(keyMoment, offset + x_interval * i);
      }
      rulerIndex[timeunit] = timeUnitIndex;
  })
  return rulerIndex;
}

function computeRulerInfo(maxNumber, timeunit, unitnumber){
  let result = {};
  let duration = DU.getDuration(timeunit, unitnumber);
  if(Math.ceil(duration.asMinutes()) <= maxNumber){
    result[TimeUnit.MINUTE] = Math.ceil(duration.asMinutes());
  }
  if(Math.ceil(duration.asMinutes() / 5) <= maxNumber){
    result[TimeUnit.MINUTES_5] = Math.ceil(duration.asMinutes() / 5);
  }
  if(Math.ceil(duration.asMinutes() / 15) <= maxNumber){
    result[TimeUnit.MINUTES_15] = Math.ceil(duration.asMinutes() / 15);
  }
  if(Math.ceil(duration.asHours()) <= maxNumber){
    result[TimeUnit.HOUR] = Math.ceil(duration.asHours());
  }
  if(Math.ceil(duration.asDays()) <= maxNumber){
    result[TimeUnit.DAY] = Math.ceil(duration.asDays());
  }
  if(Math.ceil(duration.asWeeks()) <= maxNumber){
    result[TimeUnit.WEEK] = Math.ceil(duration.asWeeks());
  }
  if(Math.ceil(duration.asMonths()) <= maxNumber){
    result[TimeUnit.MONTH] = Math.ceil(duration.asMonths());
  }
  if(Math.ceil(duration.asYears()) <= maxNumber){
    result[TimeUnit.YEAR] = Math.ceil(duration.asYears());
  }
  result.duration = duration;
  return result;
}


let rulersColor = new Map();
rulersColor.set(TimeUnit.MINUTE, 'rgb(200,80,200)')
rulersColor.set(TimeUnit.MINUTES_5, 'rgb(210,60,210)')
rulersColor.set(TimeUnit.MINUTES_15, 'rgb(220,40,220)')
rulersColor.set(TimeUnit.HOUR, 'rgb(80,80,200)')
rulersColor.set(TimeUnit.DAY, 'rgb(80,200,200)')
rulersColor.set(TimeUnit.WEEK, 'rgb(80,200,80)')
rulersColor.set(TimeUnit.WEEK, 'rgb(200,200,80)')
rulersColor.set(TimeUnit.YEAR, 'rgb(200,80,80)')
